import React from 'react'
import PropTypes from 'prop-types'
import { inject, observer } from 'mobx-react'

import Card from 'F:/Projects/apmath-tinder/src/components/_core/Card/Card'
import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'
import Link from 'F:/Projects/apmath-tinder/src/components/_core/Link/Link'
import Button from 'F:/Projects/apmath-tinder/src/components/_input/Button/Button'

import styles from './AppBar.sass'

const NavBlock ={
    student: user => (
        <nav  className={styles.nav}>
            <Link to='/find/' type='block'> 
                <Button tabIndex='-1'> Поиск научрука </Button>
            </Link>
    
            <Space left='m' />
    
            <Link to='/mentor-edit/' type='block'> 
                <Button tabIndex='-1'> Я &mdash; научый руководитель </Button>
            </Link>
        </nav>
    ),
    mentor: user => (
        <nav  className={styles.nav}>
            <Link to='/mentor-edit/' type='block'> 
                <Button tabIndex='-1'> Редактировать данные о себе </Button>
            </Link>
    
            <Space left='m' />
    
            <Link to='/logout/' type='block' highlight={true}> 
                <Button tabIndex='-1'> Выйти </Button>
            </Link>
        </nav>
    ),
} 


@inject('store')
@observer
class AppBar extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        const user = this.props.store.user
        
        return (
            <Space horizontal='m' vertical='s' className={styles.appBar}>
                {NavBlock[user.type](user)}
            </Space>
        )
    }
}

export default AppBar


