import React from 'react'
import PropTypes from 'prop-types'
import {observer} from 'mobx-react'
import {observable, action} from 'mobx'

import Card from 'F:/Projects/apmath-tinder/src/components/_core/Card/Card'
import List from 'F:/Projects/apmath-tinder/src/components/_core/List/List'
import H from 'F:/Projects/apmath-tinder/src/components/_core/Header/Header'
import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'
import TagList from 'F:/Projects/apmath-tinder/src/components/_core/TagList/TagList'
import Accordion from 'F:/Projects/apmath-tinder/src/components/_core/Accordion/Accordion'
import Link from 'F:/Projects/apmath-tinder/src/components/_core/Link/Link'

import TaskBlock from 'F:/Projects/apmath-tinder/src/components/task/Block/TaskBlock'

import styles from './MentorBlock.sass'

const propTypes = {
    name: PropTypes.string.isRequired,
    contacts: PropTypes.oneOfType([
        PropTypes.arrayOf(PropTypes.string),
        PropTypes.objectOf(PropTypes.string)
    ]).isRequired,
    
    fields: PropTypes.oneOfType([
        PropTypes.arrayOf(PropTypes.string),
        PropTypes.objectOf(PropTypes.string)
    ]).isRequired,
    
    tasks: PropTypes.oneOfType([
            PropTypes.arrayOf(PropTypes.string),
            PropTypes.objectOf(PropTypes.string)
    ]),
    hLevel: PropTypes.number,
    expandAllTasks: PropTypes.bool,
    innerSpace: PropTypes.string
}

const defaultProps = {
    tasks: [],
    expandAllTasks: false,
    hLevel: 4,
    innerSpace: 'm'
}

const TaskDivider = props => <Space horizontal={props.innerSpace}> <hr /> </Space>

const MentorBlock = observer(props => {
    
    const contactsEl = <TagList tags={props.contacts} tagProps={{transparent: true}} />
    const fieldsEl = <TagList tags={props.fields} />
    
    const taskEl = task => <TaskBlock {...task} hLevel={props.hLevel + 1} innerSpace={props.innerSpace} />


    return (
        <div>
            <Link to={`/mentor/${props._id}`} type='block'>
                <div className={styles.linkInnerSpace}>
                    <H level={props.hLevel}> {props.name} </H>
                    <Space bottom='s' />

                    {contactsEl}
                    
                    <Space bottom='s' />
                    
                    {fieldsEl}
                </div>
            </Link>
            
            { props.tasks.length ? (
                <Accordion toExpandBtn='Показать предлагаемые темы исследований' 
                           togglePos='after'
                           expanded={props.expandAllTasks}>
                    <TaskDivider innerSpace={props.innerSpace} />
                    <List data={props.tasks}
                          divider={<TaskDivider innerSpace={props.innerSpace} />}
                          item={taskEl} />
                </Accordion>) : null }
        </div>
    )
})

MentorBlock.propTypes    = propTypes
MentorBlock.defaultProps = defaultProps

export default MentorBlock


