import React from 'react'
import PropTypes from 'prop-types'
import {observer} from 'mobx-react'
import {observable, action} from 'mobx'

import Card from 'F:/Projects/apmath-tinder/src/components/_core/Card/Card'
import H from 'F:/Projects/apmath-tinder/src/components/_core/Header/Header'
import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'
import TagList from 'F:/Projects/apmath-tinder/src/components/_core/TagList/TagList'
import Accordion from 'F:/Projects/apmath-tinder/src/components/_core/Accordion/Accordion'
import Link from 'F:/Projects/apmath-tinder/src/components/_core/Link/Link'

import TaskBlock from 'F:/Projects/apmath-tinder/src/components/task/Block/TaskBlock'


const propTypes = {
    name: PropTypes.string.isRequired,
    contacts: PropTypes.oneOfType([
        PropTypes.arrayOf(PropTypes.string),
        PropTypes.objectOf(PropTypes.string)
    ]).isRequired,
    hLevel: PropTypes.number,
    innerSpace: PropTypes.string
}

const defaultProps = {
    hLevel: 4,
    innerSpace: 'm'
}

const StudentBlock = props => (
        <Space all={props.innerSpace}>
            <H level={props.hLevel}> {props.name} </H>
            <Space bottom='s' />
            <TagList tags={props.contacts} tagProps={{transparent: true}} />
        </Space>
)

StudentBlock.propTypes    = propTypes
StudentBlock.defaultProps = defaultProps

export default StudentBlock


