import React from 'react'

import H from 'F:/Projects/apmath-tinder/src/components/_core/Header/Header'
import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'
import Card from 'F:/Projects/apmath-tinder/src/components/_core/Card/Card'

import TextInput from 'F:/Projects/apmath-tinder/src/components/_input/Text/Text'
import Button from 'F:/Projects/apmath-tinder/src/components/_input/Button/Button'

import styles from './Login.sass'

const Login = props => ( 
    <Space all='m' className={styles.host}> 
        <H level={2}> Вход </H>
        <Space bottom='xxs' />
        <table><tbody>
            <tr>
                <td> <Space right='xs'> Email </Space> </td>
                <td> 
                    <TextInput id='login'
                               className={styles.input}
                               autoFocus
                               onChange={props.handleLoginChange}
                               onKeyUp={[
                                    {key: 13, cb: props.onSubmit}
                                ]}
                               placeholder='Указанный на сайте ПМ-ПУ' /> 
                </td>
            </tr>
        </tbody></table>
        <Space bottom='s' />
        <div>
            <Button onClick={props.onSubmit}> Войти </Button>
        </div>
    </Space>
)

export default Login


