import React from 'react'

import { Route, Switch, Redirect } from 'react-mobx-router'

import ErrorPage from 'F:/Projects/apmath-tinder/src/components/_utility/ErrorPage/ErrorPage'

import Auth from 'F:/Projects/apmath-tinder/src/routes/Auth/Auth'
import Login from 'F:/Projects/apmath-tinder/src/routes/Login/Login'
import Logout from 'F:/Projects/apmath-tinder/src/routes/Logout/Logout'
import FindMentor from 'F:/Projects/apmath-tinder/src/routes/FindMentor/FindMentor'
import MentorPage from 'F:/Projects/apmath-tinder/src/routes/MentorPage/MentorPage'
import MentorEdit from 'F:/Projects/apmath-tinder/src/routes/MentorEdit/MentorEdit'

const Routes = props => (
    <Switch>
        <Redirect exact form='/' to='/find/' />
        <Route exact path='/auth/:token' component={<Auth />} />
        <Route exact path='/login/' component={<Login />} />
        <Route exact path='/logout/' component={<Logout />} />
    
        <Route exact path='/find/' component={<FindMentor />} />
        <Route exact path='/mentor/:id/' component={<MentorPage />} />

        <Route exact path='/mentor-edit/' component={<MentorEdit />} />

        <Route component={<ErrorPage code={404} />} />
    </Switch>
)

export default Routes


