import React from 'react'

import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'
import ActionBox from 'F:/Projects/apmath-tinder/src/components/_utility/ActionBox/ActionBox'

import styles from './Notification.sass'

import Icon from 'F:/Projects/apmath-tinder/src/components/_utility/Icon/Icon'

const glyphs = {
    warning: require('F:/Projects/apmath-tinder/src/icons/warning.svg').default,
    error: require('F:/Projects/apmath-tinder/src/icons/error.svg').default,
    info: '',
    success: require('F:/Projects/apmath-tinder/src/icons/done.svg').default,
}

const Notification = props => {
    const classes = props.className ? styles.host + ' ' + props.className : styles.host

    return (
        <div className={classes}>
            <div> {props.message} </div>
            <Icon glyph={glyphs[props.type] || {}} className={styles.icon} />
        </div>
    )
}

export default Notification


