import React from 'react'
import PropTypes from 'prop-types'

import H from 'F:/Projects/apmath-tinder/src/components/_core/Header/Header'
import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'

const propTypes = {
    title: PropTypes.string.isRequired,
    skills: PropTypes.string.isRequired,
    desc: PropTypes.string.isRequired
}

const TaskBlock = props => (
    <Space all={props.innerSpace || 'm'}>
         <H level={props.hLevel}> {props.title} </H>
         <Space bottom='xxs' />

         <H level={props.hLevel + 1}> Описание </H>
         <Space bottom='xxs' />
         {props.desc} 
         
         <Space bottom='s' />

         <H level={props.hLevel + 1}> Необходимые навки </H>
         <Space bottom='xxs' />
         {props.skills} 
    </Space>
)

TaskBlock.propTypes = propTypes

export default TaskBlock


