import React from 'react'
import { render } from 'react-dom'
import {BrowserRouter} from 'react-mobx-router'
import {Provider} from 'mobx-react'

import Notifications from 'F:/Projects/apmath-tinder/src/components/_core/Notifications/Notifications'
import AppBar from 'F:/Projects/apmath-tinder/src/components/_core/AppBar/AppBar'
import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'

import Routes from 'F:/Projects/apmath-tinder/src/store/routes'
import store from 'F:/Projects/apmath-tinder/src/store/mainStore'

import styles from './index.sass'

const App = () => (
    <BrowserRouter> 
        <Provider store={store}>
            <div className={styles.app}>
                <Notifications />
                <Routes />
                <AppBar />
            </div>
        </Provider>
    </BrowserRouter>
)

render(<App />, document.getElementById('root'))


