import React from 'react'
import { inject, observer } from 'mobx-react'

import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'

import Notification from 'F:/Projects/apmath-tinder/src/components/_core/Notification/Notification'
import List from 'F:/Projects/apmath-tinder/src/components/_core/List/List'

import styles from './Notifications.sass'

@inject('store') @observer
class Notifications extends React.Component {
    render() {    
        return (
            <div className={styles.host}>
                <List data={this.props.store.notifications} 
                    item={Notification} 
                    divider={<Space top='s' />}
                    getKey={data => data._id} />
            </div>
        )
    }
}

export default Notifications


