import React from 'react'

import Card from 'F:/Projects/apmath-tinder/src/components/_core/Card/Card'
import Space from 'F:/Projects/apmath-tinder/src/components/_core/Space/Space'
import List from 'F:/Projects/apmath-tinder/src/components/_core/List/List'

import Checkbox from 'F:/Projects/apmath-tinder/src/components/_input/Checkbox/Checkbox'
import TagInput from 'F:/Projects/apmath-tinder/src/components/_input/TagInput/TagInput'

import MentorBlock from 'F:/Projects/apmath-tinder/src/components/mentor/Block/MentorBlock'


import styles from './FindMentor.sass'

const MentorCard = props => <Card> <MentorBlock {...props} hLevel={4} /> </Card>

const FindMentor = props => (
    <div className={styles.view}>
        <h2> Поиск научрука </h2>
        <Space bottom='s' />

        <div className={styles.filter}> 
            <Checkbox id='ownTopic'
                      autoFocus
                      checked={props.hasOwnTopicValue}
                      onChange={props.hasOwnTopicChange}>
                Своя тема 
            </Checkbox>
            <TagInput id='FieldOfStudy' 
                      placeholder='Область исследований'
                      className={styles.tagInput} 
                      tags={props.selectedFields} />
        </div>
        <Space bottom='m' />
        {
            props.mentors.length 
            ? <List data={props.mentors} item={MentorCard} getKey={data => data._id}/> 
            : 'Таких научруков нет :('
        }
    </div>
)

export default FindMentor


